.menu {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #482349;
    z-index: 1000; }

/* задаем цвет фона для контейнера nav. */
nav {

    padding: 10px 10px; }

/* убираем отступы и поля, а также list-style для "ul", */
nav ul {
    padding: 10px;
    list-style: none;
    text-align: center;
    text-transform: uppercase; }

/* применяем inline-block позиционирование к элементам навигации */
nav ul li {
    display: inline-block; }

/* стилизуем ссылки */
nav a {
    display: block;
    padding: 0 10px;
    color: #fff;
    font-size: 25px;
    line-height: 31px;
    text-decoration: none;
    font-weight: 500; }

/* изменяем цвет фона при наведении курсора */
nav a:hover {
    transition: all 0.9s;
    background-color: #7266cb;
    text-decoration: none;
    color: white; }

.space {
    padding-top: 180px;
    background-color: #241e53; }
.intro {
    padding-bottom: 120px;
    max-height: 100vh;
    overflow: hidden;
    position: relative;

    &:after {
        display: block;
        background-color: rgba(0, 0, 0, 0.8);
        position: absolute;
        top: 0;
        left: 0; }

    &-content {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        width: 100%;
        height: 50%;

        position: absolute;
        top: 0;
        left: 0; } }

iframe {
    // margin-left: 100px
    margin-top: 100px;

    &-media {
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: 0; } }

.sp-text {
    color: #ffffff;
    font-weight: bold;
    text-shadow: 0px 0px 9px rgba(0, 0, 0, 0.3);
    font-size: 100px;
    text-align: center;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal; }

.earth-space {
    height: auto;
    padding: 10px;
    line-height: 1;
    background-color: #241e53;
    position: relative; }

h2.earth-in-space {
    padding: 60px;
    font-size: 65px; }

.earth-in-space,
.articles-h2 {
    color: #e5e2eb;
    text-align: center; }

p {
    padding: 0 0 1em 0; }

.d {
    color: aqua;
    line-height: 1;
    font-size: 100px; }

.earth-space-text {

    text-align: left;
    color: white;
    line-height: 50px;
    font-size: 26px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal; }

.earth-space {
    &-text {
        .vr {
            color: #ffffff79;
            font-weight: 300; } } }

.earth-space {
    &-text {
        .ar {
            color: #ffffff79;
            font-weight: 300; } } }

#current_date_time_block {
    color: #f8f8f8;
    font-size: 22px;
    text-align: center;
    text-transform: initial;
    padding-top: 20px;
    font-weight: 300; }
