#myFooter {
    background-color: #482349;
    padding-top: 32px;

    & .container {
        text-align: center; }

    & .footer-copyright {
        margin-bottom: 35px;
        text-align: center;
        color: #ffffff; }

    & ul {
        list-style-type: none;
        padding: 0;
        margin-bottom: 18px; }

    & a {
        color: #ffffff;
        font-size: 18px; }

    & li {
        display: inline-block;
        margin: 0px 15px;
        line-height: 2; }

    & .footer-social {
        text-align: center;
        padding-top: 25px;
        padding-bottom: 25px;
        background-color: #482349; }

    & .fa {
        font-size: 36px;
        margin-right: 15px;
        margin-left: 20px;
        background-color: white;
        color: #8d1b91;
        border-radius: 51%;
        padding: 10px;
        height: 60px;
        width: 60px;
        text-align: center;
        line-height: 43px;
        text-decoration: none;
        transition: color 0.2s; }

    & .fa-facebook:hover {
        color: #2b55ff; }

    & .fa-facebook:focus {
        color: #2b55ff; }

    & .fa-google-plus:hover {
        color: red; }

    & .fa-google-plus:focus {
        color: red; }

    & .fa-twitter:hover {
        color: #00aced; }

    & .fa-twitter:focus {
        color: #00aced; } }

/* CSS used for positioning the footers at the bottom of the page. */
/* You can remove this. */

#myFooter {
    flex: 0 0 auto;
    -webkit-flex: 0 0 auto; }

