.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background-image: -moz-linear-gradient(45deg, rgba(221, 0, 255, 0.51), rgba(0, 72, 255, 0.49));

    background-image: -webkit-linear-gradient(45deg, rgba(221, 0, 255, 0.51), rgba(0, 72, 255, 0.49));

    background-image: linear-gradient(45deg, rgba(221, 0, 255, 0.51), rgba(0, 72, 255, 0.49));
    & .row {
        margin-top: 32px; }

    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px; }

    .title {
        position: relative;
        padding-bottom: 50px;
        margin-bottom: 0;
        margin-top: 38px;
        color: #ffffff;
        font-size: 65px;

        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        z-index: 2; }

    &_item_1 {
        background: url("../img/bg/stats/vr.webp") center center/cover no-repeat; }

    &_item_2 {
        background: url("../img/bg/stats/arena.webp") center center/cover no-repeat; }

    &_item_3 {
        background: url("../img/bg/stats/izol.webp") center center/cover no-repeat; }

    &_item_4 {
        background: url("../img/bg/stats/bear.webp") center center/cover no-repeat;
        margin-top: 100px; }

    &_item_5 {
        background: url("../img/bg/stats/glass.webp") center center/cover no-repeat;
        margin-top: 100px; }
    &_item_6 {
        background: url("../img/bg/stats/ar.webp") center center/cover no-repeat;
        margin-top: 100px; }
    &_item_subtitle {
        color: #0a0909;
        font-size: 14px;
        font-weight: 400;
        padding-left: 20px; }

    &_item_subtitl {
        color: #0a0909;
        font-size: 14px;
        font-weight: 800;
        text-align: center;
        padding-bottom: 10px; }

    &_item_subtitl a:hover {
        color: #2d08ffcb;
        text-decoration: none; }

    &_item_subtitl a {
        color: #000; } }
